#signupDiv{
    width: 450px;
    height: 500px;
    border: 1px solid black;
    position: absolute;
    top: -100px;
    left: 120px;
    background-color: white;
}
#signupHead1{
    font-weight: bold;
    font-size: 23px;
    position: absolute;
    right: 10px;
    top: 20px;
}
#signupHead2{
    font-size: 14px;
    position: absolute;
    color: grey;
    right: 10px;
    top: 60px;

}

#signupInputs-Div{
    position: absolute;
    top: 100px;
    padding-top: 15px;
    border-top: 1px solid lightgrey;
}
.signupInputs1{
    width: 190px;
    height: 30px;
    margin-left: 6px;
    margin-right: 6px;
    border-radius: 5px;
    border: 1px solid lightgrey;
    background-color: whitesmoke;
    font-size: 15px;
}
.signupInputs2{
    width: 400px;
    height: 30px;
    margin-top: 10px;
    border-radius: 5px;
    border: 1px solid lightgrey;
    background-color: whitesmoke;
}
.signupInputs1::placeholder,.signupInputs2::placeholder{
    text-indent:10px;
    font-size: 15px;
}
#Xbtnsignup{
    font-weight: bold;
    position: absolute;
    font-size: 25px;
    left:10px;
    top: 5px;
    border: none;
    background-color: white;
}
#saveUserBtn{
    width: 150px;
    height: 50px;
    background-color: rgb(3, 184, 3);
    border: none;
    color: white;
    font-weight: bold;
    font-size: 18px;
    position: absolute;
    bottom: 20px;
    left: 150px;
}