.App {
  text-align: center;
  overflow: auto;
  height: 100vh;
}
#menuBarDiv{
  width: 100%;
  height: 30px;
  background-color: rgb(50,100,255);
  position: absolute;
}
#menuBarSearch{
  width: 350px;
  height: 24px;
}
#staticAdv{
  width: 300px;
  height: 550px;
  /* background-color: red; */
  border: 1px solid black;
  position: absolute;
  top: 30px;
}
#staticSideMenu{
  width: 300px;
  height: 550px;
  border: 1px solid black;
  position: absolute;
  right: 0;
  top: 30px;
}


