#signing-in{
    width: 400px;
    height: 350px;
    position: absolute;
    left: 170px;
    top: 150px;
    background-color: white;
    border-radius:10px;
    box-shadow: -1px 5px 10px 2px grey;
    /* position: relative; */
}
#signing-inInputsDiv{
    border-bottom: 1px solid lightgrey;
    height:75%;
    position: relative;
}
.signinInputs{
    width: 250px;
    height: 30px;
    margin-bottom: 10px;
    font-size: 14px;
}
.signinInputs::placeholder{
    text-indent: 10px;
    font-size: 14px;
}
#signinInput1{
    margin-top: 50px;
}
#signinEnterBtn{
    width: 95%;
    height:40px;
    background-color:rgb(50,100,255);
    border:none;
    border-radius: 5px;
    color: white;
    font-size:17px;
    position: absolute;
    left: 10px;
    bottom: 30px;
}
#forgotPass-Btn{
    font-size: 13px;
    position: absolute;
    bottom:105px;
    right: 75px;
}
#signin-SignupBtn{
    width: 150px;
    height: 50px;
    background-color: rgb(3, 184, 3);
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 20px;
    font-weight: bold;
    margin-top: 10px;
}
#helper{
    width: 100%;
    height: 100%;
    display: none;
}